#root {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.App {
  display: flex;
  width: 100%;
  height: 100%;
}

.menu {
  border: none;
  background: linear-gradient(to top left, cadetblue, lightslategrey);
  margin: auto auto;
  padding: 20px;
  text-align: center;
  box-shadow: 0 0 5px 2px black;
}

.snake-container {
  margin: auto auto;
}

p {
  margin-top: 10px;
}

input {
  margin: 10px auto 20px auto;
  width: 30%;
  border: none;
  border-radius: 4px;
  display: block;
  background-color: cadetblue;
  box-shadow: 0 0 5px 2px black;
}

button {
  border: none;
  border-radius: 4px;
  animation: color_change 10s infinite;
  box-shadow: 0 2px 5px 2px black;
  cursor: pointer;
}

button:active {
  transform: translateY(2px);
  box-shadow: 0 1px 5px 1px black;
}

@keyframes color_change {
  0% {
    background-color: red;
  }
  14% {
    background-color: orange;
  }
  28% {
    background-color: yellow;
  }
  42% {
    background-color: green;
  }
  56% {
    background-color: deepskyblue;
  }
  70% {
    background-color: blue;
  }
  84% {
    background-color: darkviolet;
  }
  100% {
    background-color: red;
  }
}
